body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#board {
    margin-left: auto;
    margin-right: auto;
}
#table {
    margin-left: auto;
    margin-right: auto;
}
#options {
    width: 75%;
    display: -webkit-flex;
    display: flex;
    margin: 0 auto 0 auto;
}
#simOptions {
    margin: 0.75em 0.5em;
    border: solid 1px black;
    padding: 0.5em 1em 1em 1em;
    width: 85%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.optionsHeader {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 0.25em;
}
.optionRow {
    margin-top: 0.25em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
#buttonRow {
    width: 65%;
    margin-top: 0.25em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
#clearButton {
    color: red;
}
.row {
    margin: 0.25em 0 0.25em 0;
}
#randomOptions {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
.slider {
    height: 1em;
    margin-left: 1em;
}
#colorOptions {
    width: 15%;
    margin: 0.75em 0.5em;
    border: solid 1px black;
    padding: 0.5em 1em 1em 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
#footer {
    width: 75%;
    margin: 0 auto;
    text-align: left;
}

.cell {
    display: block;
    border: solid 1px grey;
    width: 20px;
    height: 20px;
    margin: -2.5px;
}
.App {
  text-align: center;
}
